<template>
  <div>
    <a-button @click="$refs.addEditModal.showModal()">新增收货地址</a-button>
    <address-item v-for="addr in addrList" :key="addr.id" :addr-info="addr"></address-item>
    <add-edit-modal ref="addEditModal"></add-edit-modal>
  </div>
</template>

<script>
import AddressItem from './Item'
import AddEditModal from './AddEditModal'

export default {
  name: 'myAddress',
  components: { AddressItem, AddEditModal },
  data () {
    const addrList = []
    for (let _i = 0; _i < 10; _i++) {
      addrList.push({
        id: `addr_${_i}`,
        name: `张${_i}丰`,
        isDefault: _i === 0,
        area: '山东省 青岛市 李沧区',
        addr: 'XXXX路 XX号 XXXXX小区 X-XXX-XXX',
        phone: '188****888'
      })
    }
    return {
      addrList
    }
  }
}
</script>
