<template>
  <a-modal
    title="新增编辑地址"
    :visible="visible"
    @ok="handleOk"
    :confirmLoading="confirmLoading"
    @cancel="handleCancel"
  >
    <a-form :form="form" @submit="handleSubmit">
      <a-form-item v-bind="formItemLayout" label="收货人">
        <a-input
          v-decorator="[
          'name',
          {
            rules: [{ required: true, message: '请输入收货人', whitespace: true }]
          }
        ]"
        />
      </a-form-item>
      <a-form-item v-bind="formItemLayout" label="所在区域">
        <a-cascader
          v-decorator="[
          'area',
          {
            initialValue: ['山东', '青岛', '李沧'],
            rules: [{ type: 'array', required: true, message: '请选择所在区域!' }],
          }
        ]"
          :options="residences"
        />
      </a-form-item>
      <a-form-item v-bind="formItemLayout" label="电话">
        <a-input
          v-decorator="[
          'phone',
          {
            rules: [{ required: true, message: '必须输入电话!' }],
          }
        ]"
          style="width: 100%"
        >
          <a-select
            slot="addonBefore"
            v-decorator="[
            'prefix',
            { initialValue: '86' }
          ]"
            style="width: 70px"
          >
            <a-select-option value="86">+86</a-select-option>
            <a-select-option value="87">+87</a-select-option>
          </a-select>
        </a-input>
      </a-form-item>
      <a-form-item v-bind="formItemLayout" label="地址">
        <a-input
          v-decorator="[
          'addr',
          {
            rules: [{ required: true, message: '请输入地址', whitespace: true }]
          }
        ]"
        />
      </a-form-item>
    </a-form>
  </a-modal>
</template>

<script>
const residences = [{
  value: '山东',
  label: '山东',
  children: [{
    value: '青岛',
    label: '青岛',
    children: [{
      value: '李沧',
      label: '李沧'
    }]
  }]
}, {
  value: '北京',
  label: '北京',
  children: [{
    value: '北京',
    label: '北京'
  }]
}]
export default {
  name: 'addEditAddr',
  data () {
    return {
      visible: false,
      confirmLoading: false,
      confirmDirty: false,
      form: this.$form.createForm(this),
      residences,
      autoCompleteResult: [],
      formItemLayout: {
        labelCol: {
          xs: { span: 24 },
          sm: { span: 8 }
        },
        wrapperCol: {
          xs: { span: 24 },
          sm: { span: 16 }
        }
      },
      tailFormItemLayout: {
        wrapperCol: {
          xs: {
            span: 24,
            offset: 0
          },
          sm: {
            span: 16,
            offset: 8
          }
        }
      }
    }
  },
  methods: {
    showModal (data) {
      this.visible = true
      this.$nextTick(function () {
        this.form.setFieldsValue(data)
      })
    },
    handleOk (e) {
      this.ModalText = 'The modal will be closed after two seconds'
      this.confirmLoading = true
      setTimeout(() => {
        this.visible = false
        this.confirmLoading = false
      }, 2000)
    },
    handleCancel (e) {
      console.log('Clicked cancel button')
      this.visible = false
    },
    handleSubmit  (e) {
      e.preventDefault()
      this.form.validateFieldsAndScroll((err, values) => {
        if (!err) {
          console.log('Received values of form: ', values)
        }
      })
    }
  }
}
</script>
