<template>
  <a-card class="addr-item-box" hoverable :title="addrInfo.name">
    <div slot="extra">
      <a-button
        type="primary"
        size="small"
        style="margin-right: 0.5em;"
        @click="$refs.addEditModal.showModal(addrInfo)"
      >编辑</a-button>
      <a-button type="danger" size="small" @click="deleteAddr">删除</a-button>
    </div>
    <p class="addr-item">
      <label class="item-label">收货人：</label>
      <span class="item-content">{{addrInfo.name}}</span>
    </p>
    <p class="addr-item">
      <label class="item-label">所在地区：</label>
      <span class="item-content">{{addrInfo.area}}</span>
    </p>
    <p class="addr-item">
      <label class="item-label">地址：</label>
      <span class="item-content">{{addrInfo.addr}}</span>
    </p>
    <p class="addr-item">
      <label class="item-label">电话：</label>
      <span class="item-content">{{addrInfo.phone}}</span>
    </p>
    <add-edit-modal ref="addEditModal"></add-edit-modal>
  </a-card>
</template>

<script>
import AddEditModal from './AddEditModal'

export default {
  name: 'AddressItem',
  components: { AddEditModal },
  props: {
    addrInfo: Object
  },
  methods: {
    /**
     * 删除地址
     */
    deleteAddr () {
      debugger
      this.$confirm({
        title: `确认删除${this.addrInfo.name}的地址？`,
        content: '删除后改地址不可恢复，是否确认删除？',
        okType: 'danger',
        onOk () {
          return new Promise((resolve, reject) => {
            setTimeout(Math.random() > 0.5 ? resolve : reject, 1000)
          }).catch(() => console.log('Oops errors!'))
        },
        onCancel () {}
      })
    }
  }
}
</script>

<style scoped>
.addr-item-box {
  margin-bottom: 10px;
}
.addr-item {
  font-size: 12px;
  line-height: 1;
  padding: 0;
  margin: 0;
  margin-bottom: 0.6em;
}
.item-label {
  width: 100px;
  text-align: right;
  color: #999;
  margin-right: 1em;
}
.item-content {
  color: #333;
}
</style>
